.timeline {
  width: 85%;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  padding: 0 0 0 32px;
  /* border-left: 2px solid #696969; */
  border-left: 2px solid black;
  font-size: 1.125rem;
  list-style-type: none;
}

.timeline_item {
  display: flex;
  align-items: start; /* Align items vertically */
}

.timeline_item_icon {
  display: flex;
  align-items: start;
  justify-content: center;
  width: 40px;
  height: 40px;
  /* border-radius: 50%; */
  margin-left: -52px;
  /* flex-shrink: 0; */
  overflow: hidden;
  box-shadow: 0 0 0 6px #fff;
  svg {
    width: 20px;
    height: 20px;
  }
  background-color: white;
}

.timeline_item_content {
  margin-left: 20px;
}

.headerlink {
  color: #167c9e;
  text-decoration: none;
  margin-bottom: 10%;
}

.headerlink:hover {
  color: #073f52;
  text-decoration: none;
}

.title {
  font-weight: 450;
  color: black;
}
