.outline {
  margin-left: 10%;
  margin-right: 10%;
  padding-bottom: 6%;
}
.zoom {
  /* background-color: #F0F0F0; */
  /* padding-top: 10%;
  padding-bottom: 10%; */
  /* height: 100%; */
  padding-top: 5%;
  padding-bottom: 2%;
  padding-right: 3%;
  padding-left: 3%;
  margin-bottom: 4%;
  margin-left: 2%;
  margin-right: 2%;
  border-radius: 20px;
  border-color: #c4c4c4;
  border-style: solid;
  border-width: 1px;
  transition: transform 0.2s;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  height: 415px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

h2 {
  line-height: 0.5;
  margin: 0;
  border-width: 2px;
}

.zoom:hover {
  transform: scale(
    1.05
  ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  color: #167c9e;
  border-color: #167c9e;
}

.project_title {
  margin-top: 4%;
}

.button:hover {
  cursor: default;
}
